<template>
  <div class="hello">
    <h1>{{ value }}</h1>
    <slot :progress="value"></slot>
  </div>
</template>

<script>
export default {
  name: 'Loading',
  data() {
    return {
      timer: null,
    };
  },
  props: {
    value: {
      type: Number,
      required: true,
      default: 0,
    },
    input: {
      type: Function,
    },
    time: Number,
    srcList: Array,
    
  },
  methods: {
    upData() {
      this.interval(this.value);
    },
    interval(val) {
      this.timer = setTimeout(() => {
        let num = val;
        if (val <= 98) {
          num += 2;
          this.$emit('input', num);
          clearTimeout(this.timer);
          this.timer = null;
          this.interval(num);
        } else {
          clearTimeout(this.timer);
          this.timer = null;
        }
      }, 20)
    }
  },
  mounted() {
    this.upData();
  }
}
</script>
